<template>

  <a-form  layout="vertical" :form="form" @submit="handleSubmit"  >

    <a-form-item label="Picture">
      <div class="dropbox">
    <a-upload
      name="picture"
      list-type="picture-card"
      class="avatar-uploader"
      :show-upload-list="false"
      :action="$baseUrl+'api/filePicture'"
      :before-upload="beforeUpload"
      @change="handleChange"
    >
      <img v-if="imageUrl" :src="imageUrl" alt="avatar" class="picture_avatar" />
      <div v-else>
        <a-icon :type="loading ? 'loading' : 'plus'" />
        <div class="ant-upload-text">
          Upload
        </div>
      </div>
    </a-upload>

      </div>
    </a-form-item>

    <a-form-item label="Title" :validate-status="titleError() ? 'error' : ''" :help="titleError() || ''">
      <a-input
        v-decorator="[
          'title',
          { rules: [{ required: true, message: 'Please input your title!' }] },
        ]"
        placeholder="Title"
        allow-clear
      >
        <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
      </a-input>
    </a-form-item>

    <a-form-item label="Summary" :validate-status="summaryError() ? 'error' : ''" :help="summaryError() || ''">
      <a-textarea placeholder="Basic usage" :rows="5"
        v-decorator="[
          'summary',
          { rules: [{ required: true, message: 'Please input your summary discription!' }] },
        ]"

        allow-clear

      />
    </a-form-item>

    <a-form-item >
      <a-col >
          <a-button icon="check-circle"  type="primary" html-type="submit" :disabled="hasErrors(form.getFieldsError())" >
              Save
          </a-button >
      </a-col>
    </a-form-item>

  </a-form>
</template>

<script>

function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result));
  reader.readAsDataURL(img);
}
function hasErrors(fieldsError) {
  return Object.keys(fieldsError).some(field => fieldsError[field]);
}
export default {
  data() {
    return {
      loading: false,
      imageUrl: '',
      hasErrors,
      form: this.$form.createForm(this, { name: 'horizontal_login' }),
      experiences:'',
      fileInfo:'',
      userInfo: localStorage.getItem('user'),
      userID: '',
      chefeCV: {
        title:'',
        summary:''
      }

    };
  },
  methods: {
    handleChange(info) {
      if (info.file.status === 'uploading') {
        this.loading = true;
        return;
      }
      if (info.file.status === 'done') {
        //console.log(info.file.response)
        this.fileInfo=info.file.response;
        // Get this url from response in real world.
        getBase64(info.file.originFileObj, imageUrl => {
          this.imageUrl = imageUrl;
          this.loading = false;
        });
      }
    },
    beforeUpload(file) {
      const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
      if (!isJpgOrPng) {
        this.$message.error('You can only upload JPG file!');
      }
      const isLt2M = file.size / 1024 / 1024 < 10;
      if (!isLt2M) {
        this.$message.error('Image must smaller than 10MB!');
      }
      return isJpgOrPng && isLt2M;
    },
    handleConfirmBlur(e) {
      const value = e.target.value;
      this.confirmDirty = this.confirmDirty || !!value;
    },

    // Only show error after a field is touched.
    titleError() {
      const { getFieldError, isFieldTouched } = this.form;
      return isFieldTouched('title') && getFieldError('title');
    },
    summaryError() {
      const { getFieldError, isFieldTouched } = this.form;
      return isFieldTouched('summary') && getFieldError('summary');
    },

    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          //.log('Received values of form: ', values);
          this.sendData(values);
        }
      });
    },
  sendData(data) {
      this.axios
      .post("create/cv", { data: { cvDate: data, fileData:this.fileInfo,imageUrl : this.imageUrl } })
      .then(() => {
          this.allerros = [];
          this.sucess = true;
      })
      .catch(() => {
          this.success = false;
        
      });
  },
    openNotification: function () {
        /*this.$notification.config({
            placement: 'topRight',
            top: 35,
            duration: 8,
        });
        this.$notification[type]({
          message: m,
          description: d,
        });*/
    },
    image(){
        if(this.chefeCV.picture){
            this.imageUrl = this.$baseUrl+'storage/'+this.chefeCV.picture.path+this.chefeCV.picture.name;
            this.fileInfo = {
              file_id:this.chefeCV.picture.id,
              mime_type:this.chefeCV.picture.mime_type,
              name:this.chefeCV.picture.name,
              path:this.chefeCV.picture.path
            }
        }

    }

  },
  mounted() {
    
    this.$nextTick(() => {
      // To disabled submit button at the beginning.
      this.form.validateFields();
    });

    const userData = JSON.parse(this.userInfo);
    this.userID = userData.logged_in_user.id;
  this.axios
      .get('getExperiences')
      .then(response => (this.experiences = response.data));

  this.axios
      .get('getCVData/'+this.userID)
      .then(response => (
          this.chefeCV = response.data,this.image(),
          this.form.setFieldsValue({
            title: this.chefeCV.title ,
            summary: this.chefeCV.summary
          })

      ));
  },
};
</script>
<style>
.avatar-uploader > .ant-upload {
  max-width: 300px;
  max-height: 200px;
}
.ant-upload-select-picture-card i {
  font-size: 32px;
  color: #999;
}

.ant-upload-select-picture-card .ant-upload-text {
  margin-top: 8px;
  color: #666;
}

.picture_avatar {
  max-width: 300px;
  max-height: 200px;
}
img {

    max-height: 300px;
}
</style>

