<template>
        <v-row>
            <v-col>
            <v-card
            outlined
            >
                <v-list-item>
                <v-list-item-content>
                    <div class="text-overline font-weight-medium">
                    Public Profile
                    </div>
                </v-list-item-content>
                </v-list-item>
                <v-card-text class="text--primary">
                    <div class="font-weight-light">Making your profile public you will be allowing customers to find you on the web as well as see personal data: Full name, country, post, picture, title and summary.</div>
                </v-card-text>
                <v-card-actions>
                <v-spacer></v-spacer>
                    <v-switch
                    inset
                    v-model="valueType"
                    color="orange"
                    :value="true"
                    @change="toggle($event !== null, $event)"
                    ></v-switch>
                </v-card-actions>
            </v-card>

            </v-col>
        </v-row>

</template>
<script>
export default {
    components: { },
    data() {
        return {
            type:0,
            valueType:false,

        }
    },
    methods: {
        getData(){
        this.axios
            .get('chefe/type/find')
            .then(
                response => (this.type = response.data.public,this.check() )
            );

        },
        check(){
            switch (this.type) {
                case 1:
                    this.valueType = false
                    break;
                default:
                    this.valueType = true
                    break;
            }
        },

        change($value){
            switch ($value) {
                case "true":
                    this.type=0;
                    break;

                default:
                    this.type=1;
                    break;
            }
        this.axios
            .get('chefe/type/change/'+this.type)
            .then(
                //no return
            );

        },

        toggle(value, event) {
            console.log(`${value} ${event}`);

            this.change(`${value}`);
        }

    },
    mounted() {
       this.getData()

    },

    watch: {
        valueType: function (newVal, oldVal) {
            if(newVal != oldVal ){
              //  this.change();
            }
        }
    }
}
</script>
